import axios from './axios'

// 疫情数据
export function getMapData() {
    return axios({
        method: 'get',
        url: 'epi',
        headers: {
            'token':window.sessionStorage.getItem('token')
        }
    })
}
// 学生分布数据
export function getYunnanMap() {
    return axios({
        method: 'get',
        url: 'stu'
    })
}
